@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.carList {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
  font-family: 'Inter', sans-serif;
}

.carItem {
  background: #FBE7C6;
  border: 1px solid #ddd;
  padding: 16px;
}

.carHeader {
  margin-bottom: 12px;
}

.carName {
  font-size: 16px;
  font-weight: 600;
  color: #333;
  font-family: 'Orbitron', monospace;
}

.raceTrack {
  position: relative;
  width: 100%;
  height: 80px;
  background: #2c2c2c;
  border: 3px solid #fff;
  margin-bottom: 12px;
  overflow: hidden;
}

.raceTrack::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 3px;
  background: repeating-linear-gradient(
    to right,
    #fff 0px,
    #fff 15px,
    transparent 15px,
    transparent 30px
  );
  transform: translateY(-50%);
}

.raceTrack::after {
  content: '';
  position: absolute;
  top: 0;
  right: 20px;
  bottom: 0;
  width: 4px;
  background: repeating-linear-gradient(
    to bottom,
    #fff 0px,
    #fff 8px,
    #000 8px,
    #000 16px
  );
}

.carBox {
  position: absolute;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
  z-index: 2;
  font-size: 32px;
  color: inherit;
}

.carControls {
  display: flex;
  gap: 8px;
}

.carControls button {
  padding: 8px 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  background: #fff;
}

.carControls button:first-child {
  background-color: #dc3545;
  color: white;
  border-color: #dc3545;
}

.carControls button:nth-child(2) {
  background-color: #6c757d;
  color: white;
  border-color: #6c757d;
}

.carControls button:nth-child(3) {
  background-color: #28a745;
  color: white;
  border-color: #28a745;
}

.carControls button:last-child {
  background-color: #ffc107;
  color: #000;
  border-color: #ffc107;
}

.carControls button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}